import React from 'react'
import {MDBContainer, MDBRow} from "mdbreact";
import styled from 'styled-components';

const Styled = styled.div`
  .text {
    font-family:Times New Roman;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
  }
  @media (min-width: 100px){
    .text{
      font-size: 16px;
    }
  }
  @media (min-width: 992px){
    .text{
      font-size: 22px;
    }
  }
`

class ContactUs extends React.Component{
    render(){
        return(
            <MDBContainer>
                <Styled>
                    <MDBRow className="text">
                        <text>
                            Have a request that we didn't mention? Please do not hesitate to get in touch
                            with us!! Calls are the preferred communication method and will be responded to sooner.
                            Please call between x and x Monday to Friday. If that is not available, please
                            email the address below detailing the request.<br></br><br></br>
                            Phone Number: XXXX XXX XXX<br></br>
                            Email: phillipsbootrepairs@gmail.com<br></br>
                        </text>
                    </MDBRow>
                </Styled>
            </MDBContainer>
        );
    }

}
export default ContactUs;